
@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');

header{
    background-color: #65C3BA;
    height: 5.5rem;
}
header .header__title{
    font-family: 'Pacifico', cursive;  
    transition: 0.5s;  
}
header .header__title:hover{
    color:#FE4A49 ;
}
header a:hover{
    text-decoration: none;
}
header a{
    font-size: 1.5rem;
    margin: auto 1rem;
    color: black !important;
    padding: 10px;
    border-radius: 10px;
    transition: 0.4s;
}
header ul{
    display: flex;
    list-style: none;
    justify-content: flex-end;
    align-items: center;
    margin: 0;
    height: 100%;
}
header ul li{}

.header--active{
    background-color:#FED766 ;
    color:#FE4A49 ;
}